import { login, logout, getInfo } from '@/api/login'
import { getToken, setToken, removeToken } from '@/utils/auth'

const user = {
  state: {
    token: getToken(),
    roles: [],
    basicInfo: ''
  },

  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_BASIC_INFO: (state, basicInfo) => {
      state.basicInfo = basicInfo
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles
    }
  },

  actions: {
    // 登录
    Login ({ commit }, userInfo) {
      const username = userInfo.username.trim()
      return new Promise((resolve, reject) => {
        login(username, userInfo.password).then((response) => {
          if (response.data.success === 'OK') {
            setToken(response.data.data.username)
            commit('SET_TOKEN', response.data.data.username)
            resolve()
          }
        }).catch((error) => {
          reject(error)
        })
      })
    },

    // 获取用户信息
    GetInfo ({ commit }) {
      return new Promise((resolve, reject) => {
        getInfo().then((response) => {
          if (response.data.data && response.data.data.roles.length > 0) { // 验证返回的roles是否是一个非空数组
            const role = []
            response.data.data.roles.forEach(i => role.push(i.roleName))
            commit('SET_ROLES', role)
            commit('SET_BASIC_INFO', response.data.data.user)
          } else {
            reject(new Error('getInfo: get basic info error'))
          }
          resolve(response)
        }).catch((error) => {
          console.log('error', error)
          reject(error)
        })
      })
    },

    // 登出
    LogOut ({ commit, state }) {
      return new Promise((resolve, reject) => {
        logout(state.token).then(() => {
          commit('SET_TOKEN', '')
          commit('SET_ROLES', [])
          commit('SET_BASIC_INFO', '')
          removeToken()
          resolve()
        }).catch((error) => {
          reject(error)
        })
      })
    },

    // 前端 登出
    FedLogOut ({ commit }) {
      return new Promise((resolve) => {
        commit('SET_TOKEN', '')
        removeToken()
        resolve()
      })
    }
  }
}

export default user
