import request from '@/utils/request'

export function login (username, password) {
  return request({
    url: '/family/login',
    method: 'post',
    data: {
      username,
      password
    }
  })
}

export function getInfo () {
  return request({
    url: '/family/userBasicInfo',
    method: 'get'
  })
}

export function logout () {
  return request({
    url: '/family/logout',
    method: 'post'
  })
}
