import router from '@/router/router'
import store from '@/store'
import { Message } from 'element-ui'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
import { getToken } from '@/utils/auth' // getToken from cookie

NProgress.configure({ showSpinner: false }) // NProgress Configuration

const whiteList = ['/login', '/forgetpassword/getverifycode', '/forgetpassword/changepassword'] // no redirect whitelist

router.beforeEach((to, from, next) => {
  NProgress.start() // start progress bar
  if (getToken()) {
    // determine if there has token
    /* has token */
    if (to.path === '/login') {
      next()
      NProgress.done() // if current page is dashboard will not trigger afterEach hook, so manually handle it
    } else if (store.getters.roles.length === 0) {
      // next({ path: '/login' });
      store
        .dispatch('GetInfo')
        .then((res) => {
          // 拉取user_info
          const role = []
          res.data.data.roles.forEach(i => role.push(i.roleName))
          // const { roles } = res.data; // note: roles must be a array! such as: ['editor','develop']
          store.dispatch('GenerateRoutes', role).then(() => {
            // 根据roles权限生成可访问的路由表
            router.addRoutes(store.getters.addRouters) // 动态添加可访问路由表
            next({ ...to, replace: true }) // hack方法 确保addRoutes已完成 ,set the replace: true so the navigation will not leave a history record
          })
        })
        .catch(() => {
          store.dispatch('FedLogOut').then(() => {
            Message({
              message: '登录信息已失效,请重新登录',
              type: 'error'
            })
            next({ path: '/' })
          })
        })
    } else if (store.getters.addRouters.length === 0) {
      const role = store.getters.roles
      store.dispatch('GenerateRoutes', role).then(() => {
        // 根据roles权限生成可访问的路由表
        router.addRoutes(store.getters.addRouters) // 动态添加可访问路由表
        next({ ...to, replace: true }) // hack方法 确保addRoutes已完成 ,set the replace: true so the navigation will not leave a history record
      })
    }

    next()
  } else if (whiteList.includes(to.path)) {
    // 在免登录白名单，直接进入
    next()
  } else {
    /* has no token */
    next(`/login?redirect=${to.path}`) // 否则全部重定向到登录页
    NProgress.done()
  }
})

router.afterEach(() => {
  NProgress.done() // finish progress bar
})
