import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
/**
 * 重写路由的push方法
 */
const routerPush = Router.prototype.push
Router.prototype.push = function push (location) {
  return routerPush.call(this, location).catch(error => error)
}

Router.prototype.goBack = function () {
  this.isBack = true
  window.history.go(-1)
}

// const Home = resolve => require(['@/views/home/index'], resolve)
// const TopNav = resolve => require(['@/components/baseLayout/index'], resolve)
const Login = resolve => require(['@/views/user/login'], resolve)
const AppHome = resolve => require(['@/views/user/AppHome'], resolve)
const AddPlan = resolve => require(['@/views/addplan/index'], resolve)
const AddPlanRange = resolve => require(['@/views/addplanrange/index'], resolve)
const UserInfo = resolve => require(['@/views/user/userInfo/index'], resolve)
const MakePlan = resolve => require(['@/views/makeplan/index'], resolve)
const Record = resolve => require(['@/views/record/index'], resolve)

// 所有权限通用路由表
// 如首页和登录页和一些不用权限的公用页面
export const constantRouterMap = [
  {
    path: '/',
    redirect: '/index'
  },
  {
    path: '/login',
    component: Login
  }
]

// 实例化vue的时候只挂载constantRouter
export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: constantRouterMap
})

// 异步挂载的路由
// 动态需要根据权限加载的路由表
export const asyncRouterMap = [
  {
    path: '/index',
    name: 'Index',
    meta: {
      roles: ['family']
    },
    redirect: '/record',
    component: AppHome,
    children: [
      {
        path: '/user',
        meta: {
          roles: ['family'],
          keepAlive: false
        },
        name: 'UserInfo',
        component: UserInfo
      },
      {
        path: '/record',
        meta: {
          roles: ['family'],
          keepAlive: false
        },
        name: 'Record',
        component: Record
      },
      {
        path: '/plan',
        meta: {
          roles: ['family'],
          keepAlive: false
        },
        name: 'MakePlan',
        component: MakePlan
      }

    ]
  },
  {
    path: '/addPlan',
    name: 'OctAddPlan',
    meta: {
      roles: ['family'],
      keepAlive: false
    },
    component: AddPlan
  },
  {
    path: '/addPlanRange',
    name: 'OctAddPlanRange',
    meta: {
      roles: ['family'],
      keepAlive: false
    },
    component: AddPlanRange
  },
  { path: '*', redirect: '/404', hidden: true }
]
