//  添加class .classList.add
//  内部html  innerHTML += ''
export default class DomUtil {
  static getElByClassName (that, name) {
    if (that !== null && name !== '') {
      return that.$el.getElementsByClassName(name)
    }
    return null
  }

  static getElById (that, id) {
    if (that !== null && id !== '') {
      return that.$el.getElementById(id)
    }
    return null
  }

  static getDomByClassName (that, name) {
    if (that !== null && name !== '') {
      return document.getElementsByClassName(name)
    }
    return null
  }

  static getDomById (that, id) {
    if (that !== null && id !== '') {
      return document.getElementById(id)
    }
    return null
  }
}
